body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  min-width: 100vh;
  overflow: scroll;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  min-height: 100vh;
  min-width: 100vw;
  text-align: center;
  background-color: rgba(82, 82, 82, 0.5);
  font-family: "American Typewriter";
}

.AppNav {
  float: left;
}

.ViewGrid {
  max-width: 50vw;
  width: 50vw;
  min-height: 80vh;
  height: 80vh;
  background-color: rgba(231, 231, 231, 0.5);
}

.Cart {
  max-width: 20vw;
  width: 20vw;
  min-height: 80vh;
  height: 80vh;
}

.LogoImg {
  float: right;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.UserView {
    /*min-height: 100vh;*/
    /*min-width: 90vw;*/
    float: right;
    /*display: flex;*/
    background-color: rgba(60, 30, 8, 0.36);
}

.UserMenu {
    vertical-align: top;
    /*width: 10vw;*/
    /*height: 100vh;*/
}

.UserMenuItem {
    height: 100%;
    width: 100%;
    vertical-align: top;
    background-color: #aeaeae;
}
.RestaurantCard {
    margin: 1vw;
    height: 50vh;
    width: 50vh;
    text-align: center;
    cursor: pointer;
}

.RestaurantCardContent {
    text-align: center;
    background-color: rgba(231, 231, 231, 0.31);
}

.RestaurantCardMedia {
    text-align: center;
    height: 30vh;
    width: 30vw;
}

.UserRestaurantPopover {
    height: 100vh;
    width: 100vw;
    float: right;
}

.UserRestaurantViewContainer {
    width: 100vw;
    text-align:center;
}

.UserRestaurantViewGrid {
    padding: 10vw;
    min-width: 20vw;
    width: 100vw;
}
.ActiveMenu, .ActiveMenuPopover {
    height: 100vh;
    width: 100vw;
}

.ActiveMenu {
    padding: 2vw;
}

.MenuItemCard {
    max-width: 100vw;
    max-height: 100vh;
    margin: 1vw;
}

.MenuItemCardContent {
    text-align:center;
}

.CloseButton {
    cursor: pointer;
    margin-bottom: 2vh;
}
.UserOrderViewContainer {
    width: 100vw;
    text-align:center;
}

.UserOrderViewGrid {
    padding: 10vw;
    min-width: 20vw;
    width: 100vw;
}

.OrderCard {
    min-width: 5vw;
    min-height: 5vh;
    width: 20vw;
    height: 30vh;
    padding: 4vw;
    margin: 2vw;
}
.Cart {
    width: 80vw;
    max-width: 80vw;
    min-width: 200px;
}

.CartGrid {
    padding: 2vw;
}

.CartContent {
    /*width: 80vw;*/
    max-width: 80vw;
    min-width: 200px;
    /*height: 80vh;*/
    max-height: 80vh;
    min-height: 200px;
}
